/*
 * Mini Portfolio
 * Version: 1.0.0
 */
:root {
  --hue: 196;
  --sat: 100%;
  --lig: 45%;
  --primary-color: hsl(var(--hue), var(--sat), var(--lig));
  --secondary-color: hsl(var(--hue), var(--sat), 57%);
  --text-color: hsl(var(--hue), 8%, 16%);
  --text-color-light: hsl(var(--hue), 8%, 48%);
  --text-color-lighten: hsl(var(--hue), 8%, 92%);
  --body-color: hsl(var(--hue), 0%, 100%);
  --container-color: hsl(var(--hue), 0%, 100%);
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --normal-font-size: .9375rem;
  --small-font-size: .8125rem;
  --smaller-font-size: .75rem;
  --primary-color-hover: hsl(var(--hue), var(--sat), calc(var(--lig) - 5%));
  --primary-color-pressed: hsl(var(--hue), var(--sat), calc(var(--lig) - 10%));
  --text-color-lighten-hover: hsl(var(--hue), 8%, 95%);
  --gutter-x: 0.5rem;
  --gutter-y: 1rem;
}
@media only screen and (min-width: 992px) {
  :root {
    --h1-font-size: 1.625rem;
    --h2-font-size: 1.375rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .8125rem;
  }
}

.btn-rounded, .btn {
  border-radius: 8px;
}

::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-track {
  border-radius: 8px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: hsl(var(--hue), 8%, 64%);
  border: 3px solid transparent;
  background-clip: padding-box;
}

::-webkit-scrollbar-thumb:hover {
  border-radius: 8px;
  background-color: hsl(var(--hue), 8%, 56%);
}

body.dark-theme::-webkit-scrollbar-thumb {
  background-color: hsl(var(--hue), 8%, 24%);
}
body.dark-theme::-webkit-scrollbar-thumb:hover {
  background-color: hsl(var(--hue), 8%, 32%);
}

/* Elements */
* {
  margin: 0;
  padding: 0;
}

::selection {
  background-color: var(--primary-color);
  color: var(--body-color);
}

html,
body {
  font-family: "Poppins", sans-serif;
  font-size: var(--normal-font-size);
  font-weight: 400;
  color: var(--text-color);
  line-height: 150%;
}

body {
  background-color: var(--body-color);
  min-height: 100vh;
  transition: all 0.3s ease 0s;
}

p {
  margin: 0;
}

a {
  font-family: "Poppins", sans-serif;
  font-size: var(--normal-font-size);
  font-weight: 400;
  color: var(--primary-color);
  line-height: 150%;
  transition: all 0.3s ease 0s;
  color: var(--primary-color);
  text-decoration: none;
  outline: none !important;
}
a:hover, a:focus {
  color: var(--primary-color-hover);
  text-decoration: none;
}
a:active {
  color: var(--primary-color-pressed);
}

small {
  font-size: var(--small-font-size);
}

img {
  user-select: none;
  -webkit-user-drag: none;
}

section {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0;
}

h1 {
  font-size: var(--h1-font-size);
}

h2 {
  font-size: var(--h2-font-size);
}

h3 {
  font-size: var(--h3-font-size);
}

.reveal-top {
  visibility: hidden;
}

button {
  transition: all 0.3s ease 0s;
}
button:focus, button:hover, button:active {
  outline: none;
}

.btn {
  padding: 1rem 1.5rem;
  transition: all 0.3s ease 0s;
  text-decoration: none;
  font-weight: 500;
}
.btn.btn-nowrap {
  white-space: nowrap;
}
.btn.btn-primary {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  color: var(--text-color-lighten);
  box-shadow: 0 8px 24px hsla(var(--hue), var(--sat), var(--lig), 0.25);
}
.btn.btn-primary:hover, .btn.btn-primary:focus {
  background-color: var(--primary-color-hover);
  border-color: var(--primary-color-hover);
  color: var(--text-color-lighten-hover);
  box-shadow: none;
}
.btn.btn-primary:active {
  background-color: var(--primary-color-pressed);
  border-color: var(--primary-color-pressed);
}

.container--custom {
  padding-right: 1.25rem;
  padding-left: 1.25rem;
  max-width: 720px;
}
@media only screen and (max-width: 374px) {
  .container--custom {
    padding-right: 1rem;
    padding-left: 1rem;
  }
}
.container--inner {
  padding-right: 0;
  padding-left: 0;
}
@media only screen and (min-width: 375px) {
  .container--inner {
    max-width: 375px;
  }
}

body.dark-theme {
  --text-color: hsl(var(--hue), 12%, 92%);
  --text-color-light: hsl(var(--hue), 12%, 64%);
  --text-color-lighten: hsl(var(--hue), 12%, 16%);
  --body-color: hsl(var(--hue), 100%, 6%);
  --container-color: hsl(var(--hue), 24%, 12%);
  --primary-color-hover: hsl(var(--hue), var(--sat), calc(var(--lig) + 5%));
  --primary-color-pressed: hsl(var(--hue), var(--sat), calc(var(--lig) + 10%));
  --text-color-lighten-hover: hsl(var(--hue), 12%, 24%);
}
body.dark-theme .btn.btn-primary {
  --text-color: hsl(var(--hue), 8%, 16%);
  --text-color-light: hsl(var(--hue), 8%, 48%);
  --text-color-lighten: hsl(var(--hue), 8%, 92%);
  --text-color-lighten-hover: hsl(var(--hue), 8%, 95%);
}

.theme-toggle-button {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  font-size: 1.25rem;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}
.theme-toggle-button:hover {
  color: var(--primary-color);
}

.profile {
  padding-top: 4rem;
  padding-bottom: 2rem;
}
.profile-inner-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
}
.profile-picture {
  position: relative;
  width: 144px;
  aspect-ratio: 1/1;
  border: 4px solid var(--primary-color);
  padding: 4px;
  border-radius: 50%;
  margin-bottom: 1.5rem;
}
.profile-picture__image {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}
.profile__name {
  text-align: center;
  font-weight: 600;
  margin-bottom: 0.25rem;
}
.profile__title {
  text-align: center;
  font-weight: 500;
  color: var(--text-color-light);
}
.profile-social {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  margin: 1.25rem 0 0;
  padding: 0;
  list-style: none;
}
.profile-social__item:not(:last-child) {
  margin-right: 0.75rem;
}
.profile-social__link {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  width: 28px;
  aspect-ratio: 1/1;
  padding: 0;
  border-radius: 8px;
  background-color: var(--text-color-lighten);
  color: var(--text-color);
  font-size: 1rem;
}
.profile-social__link:hover {
  background-color: var(--text-color-lighten-hover);
  color: var(--primary-color);
  box-shadow: 0 6px 16px hsla(var(--hue), 8%, 64%, 0.125);
}
.profile-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  align-self: stretch;
  margin-top: 2.25rem;
  margin-right: calc(0px - var(--gutter-x));
  margin-left: calc(0px - var(--gutter-x));
  margin-bottom: 0;
  padding: 0;
  list-style: none;
}
.profile-info__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: nowrap;
  padding-right: var(--gutter-x);
  padding-left: var(--gutter-x);
  flex: 1 0 0%;
}
.profile-info__name {
  text-align: center;
  font-weight: 500;
  font-size: var(--small-font-size);
  color: var(--text-color-light);
  margin-bottom: 0.25rem;
}
.profile-info__value {
  text-align: center;
  font-weight: 600;
  color: var(--text-color);
}
.profile-cta {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  align-self: stretch;
  margin-right: calc(0px - var(--gutter-x));
  margin-left: calc(0px - var(--gutter-x));
  margin-top: calc(1.75rem - var(--gutter-y));
  margin-bottom: 0;
}
.profile-cta__item {
  padding-right: var(--gutter-x);
  padding-left: var(--gutter-x);
  margin-top: var(--gutter-y);
}
.profile-cta__item--main {
  flex: 1 0 0%;
}
.profile-cta__item--flex {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
}
.profile-cta__item--flex > * {
  margin-top: 0;
}
.profile-cta__item--flex > *:first-child {
  padding-left: 0;
  padding-right: 0.25rem;
}
.profile-cta__item--flex > *:last-child {
  padding-left: 0.25rem;
  padding-right: 0;
}
.profile-cta__btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
}
.profile-cta__btn--main {
  padding: 0.875rem 1.5rem;
  font-weight: 500;
}
.profile-cta__btn--main i {
  font-weight: normal;
  margin-left: 0.5rem;
  font-size: 1.25rem;
}
.profile-cta__btn--square {
  width: 48px;
  aspect-ratio: 1/1;
  padding: 0;
  border-radius: 8px;
  background-color: var(--text-color-lighten);
  color: var(--text-color);
  font-size: 1.25rem;
}
.profile-cta__btn--square:hover {
  background-color: var(--text-color-lighten-hover);
  color: var(--primary-color);
}

.main {
  padding-top: 2rem;
  padding-bottom: 4rem;
}
.main-inner-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
}

.tab-nav {
  --icon-size: 1.25rem;
  --icon-color: var(--text-color);
  --text-display: none;
  --icon-display: block;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: center;
  flex-wrap: nowrap;
  background-color: var(--text-color-lighten);
  border-radius: 8px;
  padding: var(--gutter-x) calc(var(--gutter-x) / 2);
  align-self: stretch;
}
.tab-nav__item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  margin-right: calc(var(--gutter-x) / 2);
  margin-left: calc(var(--gutter-x) / 2);
  background-color: transparent;
  border-radius: 8px;
  height: 48px;
  border: none;
  outline: none;
  box-shadow: none;
  cursor: pointer;
  transition: flex 0.3s ease 0s;
}
.tab-nav__item:not(.active) {
  aspect-ratio: 1/1;
}
.tab-nav__item.active {
  background-color: var(--body-color);
  flex: 1 0 0%;
  --text-display: block;
  --icon-size: 1.25rem;
  --icon-display: none;
}
.tab-nav__item:hover {
  background-color: var(--body-color);
}
.tab-nav__icon {
  display: var(--icon-display);
  font-size: var(--icon-size);
  color: var(--icon-color);
}
.tab-nav__text {
  display: var(--text-display);
  font-weight: 500;
  margin-left: 0.5rem;
}
.tab-content {
  width: 100%;
}
.tab-content__item {
  display: none;
}
.tab-content__item.show {
  display: block;
}

.projects {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: center;
  flex-wrap: wrap;
  margin-right: calc(0px - var(--gutter-x));
  margin-left: calc(0px - var(--gutter-x));
  margin-top: calc(0px - var(--gutter-y));
}
.projects__item {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: nowrap;
  padding-right: var(--gutter-x);
  padding-left: var(--gutter-x);
  margin-top: var(--gutter-y);
  flex: 0 0 auto;
  width: 50%;
}
@media only screen and (max-width: 575px) {
  .projects__item {
    width: 100%;
  }
}
.projects__item--portrait {
  align-self: stretch;
}

.project {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  aspect-ratio: 3/2;
  border-radius: 16px;
  --modal-transform: translateY(100%);
}
.project:hover {
  --modal-transform: translateY(0%);
}
.project-inner-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  position: relative;
  margin-top: calc(0px - var(--gutter-y));
}
.project-inner-container > * {
  flex: 1 0 0%;
  width: 100%;
  padding-right: 0;
  padding-left: 0;
}
.project--portrait {
  aspect-ratio: unset;
  height: 100%;
}
@media only screen and (max-width: 575px) {
  .project--portrait {
    aspect-ratio: 3/2;
  }
}
.project__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.project__modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 1.5rem 1.25rem;
  transform: var(--modal-transform);
  background: linear-gradient(180deg, hsla(var(--hue), var(--sat), var(--lig), 0.1) 0%, hsla(var(--hue), 24%, 4%, 1) 95%);
  transition: transform 0.3s ease 0s;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  flex-wrap: nowrap;
}
.project__subtitle {
  color: hsl(var(--hue), 0%, 92%);
  font-size: var(--smaller-font-size);
}
.project__title {
  color: hsl(var(--hue), 0%, 100%);
  font-weight: 600;
}
.project__link {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  width: 36px;
  aspect-ratio: 1/1;
  padding: 0;
  border-radius: 8px;
  background-color: var(--primary-color);
  color: hsl(var(--hue), 0%, 100%);
  font-size: 1.375rem;
  margin-top: 1.75rem;
}

.skills {
  --skills-gutter-x: 2rem;
  --skills-gutter-y: 4rem;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  margin-right: calc(0px - var(--skills-gutter-x));
  margin-left: calc(0px - var(--skills-gutter-x));
  margin-top: calc(0px - var(--skills-gutter-y));
}
@media only screen and (max-width: 575px) {
  .skills {
    --skills-gutter-x: 0.5rem;
  }
}
.skills__column {
  padding-right: var(--skills-gutter-x);
  padding-left: var(--skills-gutter-x);
  margin-top: var(--skills-gutter-y);
  flex: 0 0 auto;
  width: 50%;
}
@media only screen and (max-width: 575px) {
  .skills__column {
    width: 100%;
  }
}
.skills__title {
  color: var(--text-color);
  font-weight: 600;
  text-align: center;
  margin-bottom: 2rem;
}
.skills__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-right: calc(0px - var(--gutter-x));
  margin-left: calc(0px - var(--gutter-x));
  margin-top: calc(0px - var(--gutter-y));
}
@media only screen and (max-width: 575px) {
  .skills__list {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
  }
}
.skills__item {
  padding-right: var(--gutter-x);
  padding-left: var(--gutter-x);
  margin-top: var(--gutter-y);
  flex: 0 0 auto;
  width: 50%;
}
@media only screen and (min-width: 375px) and (max-width: 575px) {
  .skills__item {
    width: 30%;
  }
}
@media only screen and (max-width: 374px) {
  .skills__item {
    width: 35%;
  }
}

.skill {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: nowrap;
}
.skill__icon {
  font-size: 1.25rem;
  color: var(--primary-color);
  margin-right: 0.5rem;
}
.skill__name {
  color: var(--text-color);
  font-weight: 500;
  white-space: nowrap;
}
.skill__level {
  color: var(--text-color-light);
  font-size: var(--smaller-font-size);
}

.experiences {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: nowrap;
  margin-top: calc(0px - var(--gutter-y));
  margin-bottom: calc(0px - var(--gutter-y));
}

.experience {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: nowrap;
  margin-right: calc(0px - var(--gutter-x));
  margin-left: calc(0px - var(--gutter-x));
  --line-from-top: 0;
  --line-from-bottom: 0;
  --line-order: 0;
  --content-order: 1;
}
.experience > * {
  padding-right: var(--gutter-x);
  padding-left: var(--gutter-x);
}
.experience:nth-child(odd) {
  justify-content: flex-end;
}
@media only screen and (max-width: 575px) {
  .experience:nth-child(odd) {
    justify-content: center;
  }
}
.experience:nth-child(even) {
  justify-content: flex-start;
  --line-order: 1;
  --content-order: 0;
}
@media only screen and (max-width: 575px) {
  .experience:nth-child(even) {
    justify-content: center;
    --line-order: 0;
    --content-order: 1;
  }
}
.experience:first-child {
  --line-from-top: 50%;
}
.experience:last-child {
  --line-from-bottom: 50%;
}
.experience__line-column {
  flex: 0 0 auto;
  width: 12%;
  order: var(--line-order);
}
@media only screen and (max-width: 374px) {
  .experience__line-column {
    width: 8%;
  }
}
.experience__line {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}
.experience__line::before {
  content: "";
  position: absolute;
  top: var(--line-from-top);
  bottom: var(--line-from-bottom);
  width: 2px;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--text-color-lighten);
  z-index: -1;
}
@media only screen and (max-width: 575px) {
  .experience__line::before {
    left: 6px;
  }
}
.experience__line::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  aspect-ratio: 1/1;
  border-radius: 50%;
  background-color: var(--body-color);
  border: 3px solid var(--primary-color);
  outline: 3px solid var(--body-color);
  z-index: 2;
}
@media only screen and (max-width: 575px) {
  .experience__line::after {
    left: 0;
    transform: translate(0, -50%);
  }
}
.experience__content-column {
  flex: 0 0 auto;
  width: 44%;
  order: var(--content-order);
}
@media only screen and (min-width: 375px) and (max-width: 575px) {
  .experience__content-column {
    width: 64%;
  }
}
@media only screen and (max-width: 374px) {
  .experience__content-column {
    width: unset;
    flex: 1 0 0%;
  }
}
.experience__content {
  width: 100%;
  padding: 1rem 1.25rem;
  background-color: var(--text-color-lighten);
  border-radius: 8px;
  margin: var(--gutter-y) 0;
}
.experience__title {
  color: var(--text-color);
  font-weight: 500;
}
.experience__company {
  color: var(--text-color-light);
  font-size: var(--small-font-size);
}
.experience__period {
  color: var(--text-color);
  font-size: var(--smaller-font-size);
  text-align: end;
  margin-top: 0.25rem;
}

.about__title {
  color: var(--text-color);
  font-weight: 600;
  text-align: center;
  margin-bottom: 2rem;
}
.about p {
  margin-bottom: 1.25rem;
  color: var(--text-color-light);
  text-align: center;
}